import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import AppsIcon from '@material-ui/icons/Apps';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import { ThemeToggler } from './ThemeToggler';
import { CourseFilter } from './CourseFilter';
import { PlaybackRateSwitch } from './PlaybackRateSwitch';

const useStyles = makeStyles(theme => ({
  toolbar: {
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
  },
  icon: {
    marginRight: theme.spacing(1),
  },
  filter: {
    marginRight: theme.spacing(2),
  },
  pbr: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  mainTitle: {
    'fontWeight': 'bold',
    '& > sup': {
      fontSize: '80%',
    },
  },
  subTitle: {
    verticalAlign: 'top',
  },
  offset: theme.mixins.toolbar,
}));

/**
 * @param {object} props
 * @param {string} props.title 标题
 * @param {CourseEntity} [props.course] 课程对象
 * @param {() => void} [props.goBack] 返回回调函数
 */
export function TitleBar({ title, course, goBack }) {
  const classes = useStyles();

  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
  });

  const revealCourseBrief = useScrollTrigger({
    disableHysteresis: true,
    threshold: 80,
  });

  return (
    <>
      <AppBar position="fixed" elevation={trigger ? 4 : 0}>
        <Toolbar variant="regular" className={classes.toolbar}>
          {!goBack && (
            <IconButton color="inherit" className={classes.icon} disableRipple>
              <AppsIcon />
            </IconButton>
          )}
          {Boolean(goBack) && (
            <IconButton color="inherit" className={classes.icon} onClick={goBack}>
              <ArrowBackIcon />
            </IconButton>
          )}
          <Typography variant="inherit" display="block" noWrap className={classes.title}>
            <Typography variant="h6" color="inherit" className={classes.mainTitle}>
              {title}
            </Typography>
            {Boolean(revealCourseBrief && course) && (
              <Typography variant={'caption'} color="inherit" className={classes.subTitle}>
                {[course.title, course.description].filter(Boolean).join(' · ')}
              </Typography>
            )}
          </Typography>

          {Boolean(!course) && (
            <div className={classes.filter}>
              <CourseFilter />
            </div>
          )}
          {Boolean(course) && (
            <div className={classes.pbr}>
              <PlaybackRateSwitch />
            </div>
          )}
          <ThemeToggler />
        </Toolbar>
      </AppBar>

      {/* @see https://material-ui.com/components/app-bar/#fixed-placement */}
      <div className={classes.offset} />
    </>
  );
}
